
* {
  margin: 0;
  padding: 0;
}
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    width: 100%;
    height: 100%;
    position: absolute;
    background: url(/static/img/background.24ce209.jpeg) no-repeat;
    background-size: 100% 100%;
}



.wrap {
.fade-enter-active,
		.fade-leave-active {
			-webkit-transition: opacity .5s;
			transition: opacity .5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
		  	opacity: 0;
}
,
		.fall-enter-active,
		.fall-leave-active {
			-webkit-transition: opacity .5s;
			transition: opacity .5s;
}
.fall-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
		  	opacity: 0;
}
}

.lantern {
  margin-top: 100px;
  position: relative;
}
.lantern .el-carousel__item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.lantern .el-carousel__item .img-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  border-radius: 20px;
}
.lantern .el-carousel__item .img-item img {
  width: 100%;
  height: 100%;
  border-radius: 20px;
  margin: 0;
}
.lantern .el-carousel__item .img-item .stuname {
  margin-top: -50px;
  height: 50px;
  text-shadow: 0 0 5px #fff;
  font-size: 24px;
  font-weight: 500;
}
.lantern .el-carousel__mask {
  opacity: 0.6;
}
.lantern .el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 200px;
  margin: 0;
}
.lantern .el-carousel__item:nth-child(2n) {
  /*background-color: #99a9bf;*/
  opacity: 1;
}
.lantern .el-carousel__item:nth-child(2n+1) {
  /*background-color: #d3dce6;*/
  opacity: 1;
}
@media screen and (max-width: 480px) {
.lantern {
    margin-top: 180px;
}
.el-carousel__item {
    width: 300px;
    height: 260px;
}
.el-carousel__container {
    margin-left: -50px;
    height: 300px !important;
}
.el-carousel__indicators,
  .el-carousel__arrow {
    display: none;
}
}

.fall {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.card {
  position: absolute;
  width: 116px;
  height: 156px;
  opacity: 0;
}
.card .preview {
  width: 100%;
  height: 100%;
}
.card .preview img {
  width: 100%;
  height: 100%;
  border-radius: 20px;
  margin: 0;
}
.bounce-enter-active {
  -webkit-animation: dhy-card 1.5s;
          animation: dhy-card 1.5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}
@-webkit-keyframes dhy-card {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes dhy-card {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
